<template>
  <div class="login">
    <Form ref="formCustom" :model="formData" :rules="rule" :label-width="140">
      <FormItem label="邮箱" prop="account">
        <Input type="text" v-model="formData.account" placeholder="请输入注册邮箱"/>
      </FormItem>
      <FormItem label="密码" prop="password">
        <Input type="password" v-model="formData.password" />
      </FormItem>
      <FormItem>
        <Button class="loginbtn" @click="handleReset('formCustom')">
          登 录
        </Button>
      </FormItem>
      <FormItem class="forget">
        <span @click="gotoForgetPassword">忘记密码？</span>
        <span @click="gotoRegister" class="register">注册</span>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { login } from "@/api/user.js";
import { setToken } from "@/utils/auth";
export default {
  name: "Login",
  data() {
    const validatePhone = (rule, value, callback) => {
      const reg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
      const email = /^([.a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/;
      if (reg.test(value)) {
        callback();
      } else if (email.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确的邮箱或手机号"));
      }
    };
    const validatePasword = (rule, value, callback) => {
      const passWord = /^.{6,}$/;
      if (passWord.test(value)) {
        callback();
      } else {
        callback(new Error("请输入6位以上的密码"));
      }
    };
    return {
      formData: {
        account: "",
        password: ""
      },
      rule: {
        account: [
          { required: true, message: "账号不能为空", trigger: "blur" },
          { validator: validatePhone, trigger: "blur" }
        ],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { validator: validatePasword, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    handleReset(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const params = {
            account: this.formData.account,
            pwd: this.formData.password
          };
          login(params).then(res => {
            this.$Message.success("登录成功");
            setToken(res.data.token);
            // this.$router.replace({
            //   path: "/Home"
            // });
            window.location.href = '/Home'
          });
        } else {
          this.$Message.error("登录失败!");
        }
      });
    },
    gotoForgetPassword() {
      this.$router.replace({
        path: "/forgetPassword"
      });
    },
    gotoRegister() {
      this.$router.replace({
        path: "/register"
      });
    }
  }
};
</script>

<style lang="less" scoped>
  .login{
    margin-bottom: 130px;
    .loginbtn {
      background: @warn;
      border-color: @warn;
      color: #fff;
      width: 100%;
      height: 100%;
      &:hover {
        color: #fff;
      }
      /deep/span {
        font-size: 18px;
      }
    }
    .forget {
      /deep/.ivu-form-item-content {
        color: #e0e0e0;
        cursor: pointer;
        display: flex;
        justify-content: space-between;
        font-size: 18px;
        a {
          color: #e0e0e0;
        }
        .register {
          color: @warn;
          letter-spacing: 8px;
        }
      }
    }
  }
</style>
